const MESSAGE_ERROR = 'MESSAGE-ERROR';

export const setMessageError = (messageErr) => {
    return {
        type: MESSAGE_ERROR,
        messageErr

    }
}

let initialState = {
  messageError: ''
}
const welcomeReducer = (state = initialState, action) => {
    switch (action.type) {
        case MESSAGE_ERROR: {
                return {
                    ...state,
                    messageError: action.messageErr
                };
        }
        default: return state;
    }
}

export default welcomeReducer;