import React from "react";
import Machine from './machine'
import {compose} from "redux";
import {connect} from "react-redux";
import {withRouter} from "react-router-dom";
import {getNumMachine, updateParameter} from "../../redux/parametersReducer";
import {AuthRedirect} from "../hooks/use-auth";

const MachineContainer = (props) => {
    props.getNumMachine(props.match.params.number);
    let mach = props.currentMachine.filter(m => String(m.number) === String(props.match.params.number))[0]
    return <Machine currentMachine={mach} updateParameter={props.updateParameter}  />
}

let mapStateToProps = (state) => {
    return {
        currentMachine: state.ParamsPage.machine
    }
}

export default compose(
    connect(mapStateToProps, {
        getNumMachine, updateParameter})
    ,withRouter, AuthRedirect) (MachineContainer);
