const UPDATE_PARAM = 'UPDATE-PARAM';
const TEMP_PARAMS = 'TEMP-PARAMS';

export const updateParameter = (value, machineId, param, dateEditReducer) => {

 return {
  type: UPDATE_PARAM,
  value,
  machineId,
  param,
  dateEditReducer
 }
}

export const getNumMachine = (numberMachine) => {
 return {
  type: TEMP_PARAMS,
  numberMachine
 }
}

